import styled from "styled-components"
import {palette, typeScale} from "../../utils/elements"
import { Link } from "gatsby"

const activeClassName = 'active';

export const HeaderWrapper = styled.header`
  transition: 500ms ease-in-out;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  z-index: 3;
  background-color: ${palette.grayish};
  @media only screen and (max-width: 600px){
  a:hover{
  color: #cb8db9;
  }
  }
  .gatsby-image-wrapper{
  width: 70px;
  @media only screen and (max-width: 600px){
  margin-left: 5vw;
  width: 50px;
  }
  }
  @media only screen and (min-width: 600px){
    align-items: center;
    padding: 10px 5%;
    background-color: transparent;
  }
`;

export const MenuButton  = styled.div`
  margin-right: 5vw;
  >div:first-child {
      transform: ${({ open }) => open ? 'rotate(46deg) translateY(15px)' : 'rotate(0) translateY(0)'};
    }
  >div:nth-child(2) {
      opacity: ${({ open }) => open ? '0' : '1'};
      transform: ${({ open }) => open ? 'translateX(20px)' : 'translateX(0)'};
    }
  >div:nth-child(3) {
      transform: ${({ open }) => open ? 'rotate(-46deg) translateY(-14px)' : 'rotate(0)'};
    }
  @media only screen and (min-width: 600px){
    display: none;
  }
`
export const MenuBar = styled.div`
  width: 30px;
  height: 3px;
  background-color: black;
  transition: all 0.3s linear;
`;

export const Nav = styled.nav`
  position: absolute;
  transition: 300ms ease-in-out;
  width: 100vw;
  text-transform: uppercase;
  transform-origin: top;
  transform: ${props => props.show ? 'translateY(120px)' : 'translateY(-200px)'};
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: ${palette.grayish};
  padding: 30px 0;
  *{
  margin-bottom: 10px;
  }
  z-index: -1;
  
  & *:last-of-type{
  margin-right: 0;
  }
  @media only screen and (min-width: 600px){
    position: relative;
    width: auto;
    flex-direction: row;
    background-color: transparent;
    padding: 0;
    *{
      margin-right: 20px;
      margin-bottom: 0;
    }
    transform: none;
  }
`;

export const NavItem = styled(Link).attrs({
  activeClassName: activeClassName,
})`
  font-size: ${typeScale.subtext};
  text-decoration: none;
  position: relative;
  color: #cb8db9;
   &.${activeClassName} {
   color: ${palette.byzantium};
   font-weight: bold;
  }
  &:hover{
    color: white;
  }
  @media only screen and (min-width: 600px){
    color: #acacac;
     &.${activeClassName} {
    color: white;
  }
  }
`;
